.div-center-footer-logo{
  display: flex;
  justify-content: center;

}
.logo-footer-img{
  width: 70px;
  height: 70px;
}
footer {
  background: #BB7944;
  color: #ffffff;
  display: grid;
  grid-template-columns: 1fr 1fr 1fr;
  text-align: center;
  align-items: center;
  padding: 10px;
  margin-top: 30px;
}

footer ul {
  list-style: none;
  justify-items: center;
  display: grid;
  padding: 0;
  margin: 0;
  grid-template-columns: 1fr 1fr 1fr;
}

footer ul li a {
  color: #ffffff;
  font-size: 3.5rem;
}

footer p {
  color: #ffffff;
  font-size: 1.2rem;
}